.trails {

  &__list {

    margin-top: -120px;
  }


  &__item {
    border-radius: 2px;
    box-shadow: -6px 7px 5px -2px rgba(0,0,0,0.69);
    -webkit-box-shadow: -6px 7px 5px -2px rgba(0,0,0,0.69);
    -moz-box-shadow: -6px 7px 5px -2px rgba(0,0,0,0.69);
    transition: 0.5s;
    max-width: 220px;
    background-color: $c-white;
    width: 100%;
    margin-right: 28px;

    @media (min-width:500px) {
      max-width: 315px;
    }

    &:hover {
      box-shadow: -8px 9px 5px -2px rgba(0,0,0,0.69);
      -webkit-box-shadow: -8px 9px 5px -2px rgba(0,0,0,0.69);
      -moz-box-shadow: -8px 9px 5px -2px rgba(0,0,0,0.69);
    }

    article {
      padding: 0 13px 13px 13px;
    }

    &--button {
      width: 100%;
      display: flex;
      justify-content: flex-end;

      button {
        background-color: $c-green;
        color: $c-bg-blue;
        cursor: pointer;
        height: 40px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border: {
          style: none;
          radius: 10px;
        };
        font: {
          size: 18px;
          weight: 600;
        };
        transition: 0.3s;
        &:hover {
          background-color: darken($c-green, $amount: 5);
        }
        @media (min-width: 1024px) {
          font-size: 24px;
          height: 60px;
          max-width: 179px;
          border-radius: 20px;
        }
      }
    }

    &--image {
      width: 100%;
      height: 122px;
      margin-bottom: 18px;
      overflow: hidden;
      img {
        margin-top: -10%;
        width: 100%;
      }
    }

    &--title {
      font: {
        size: 32px;
        weight: normal;
      }
      line-height: 40px;
      margin-bottom: 19px;
      @media (min-width:500px) {
        font-size: 48px;
        line-height: 59px;
      }
    }

    &--description {
      color: $c-black;
      font: {
        size: 18px;
        weight: normal;
      };
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;  
      overflow: hidden;
      line-height: 22px;
      margin-bottom: 29px;
      max-width: 283px;
    }
  }

  
}